import React from 'react';
import { connect, ConnectedProps } from 'react-redux';
import { RootAppState } from 'store/types';
import { getContents } from 'store/contents/selectors';
import ContentList from 'components/contents/ContentList/ContentList';
import { TabContent as TabContentItem } from './TabBar.styled';

type TabContentProps = {
  inverted: boolean;
  itemId: string;
  childrenElements: any[];
  content: string;
  linkTabIndex?: number;
};

class TabContent extends React.PureComponent<TabContentProps & ConnectedProps<typeof connector>> {
  render() {
    const { inverted, childrenContents, linkTabIndex } = this.props;
    return (
      <TabContentItem>
        {childrenContents.length > 0 && (
          <ContentList
            items={childrenContents}
            isLoading={false}
            inverted={inverted}
            linkTabIndex={linkTabIndex}
          />
        )}
      </TabContentItem>
    );
  }
}

function mapStateToProps(state: RootAppState, ownProps: TabContentProps) {
  return {
    childrenContents: getContents(state, ownProps.childrenElements)
  };
}

const connector = connect(mapStateToProps);
export default connector(TabContent);
